<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
      <p><span class=rvts17>角色创建：</span></p>
<p class=rvps2><span class=rvts31>起源：</span>
<br><img width=1300 height=731 alt="" hspace=1 vspace=1 src="../../assets/pic1/abcamber19.jpg"><span class=rvts20></span>
<br><span class=rvts20>1.</span><span class=rvts21>变更种族或查看起源故事</span>
<br><span class=rvts20>2.</span><span class=rvts21>查看天赋和特殊技能</span>
<br><span class=rvts20>3.</span><span class=rvts21>变更职业</span>
<br><span class=rvts20>4.</span><span class=rvts21>变更性别</span>
<br><span class=rvts20>5.</span><span class=rvts21>更改选项卡</span>
<br><span class=rvts20></span>
<br><span class=rvts32>外表：</span>
<br><img width=1300 height=731 alt="" hspace=1 vspace=1 src="../../assets/pic1/abcamber20.jpg"><span class=rvts20></span>
<br><span class=rvts21>在这里可以变更选择喜欢的肤色、脸型、发型、发色、胡子、声音。</span>
<br><span class=rvts20></span>
<br><span class=rvts32>预设：</span>
<br><img width=1300 height=731 alt="" hspace=1 vspace=1 src="../../assets/pic1/abcamber21.jpg"><span class=rvts20></span>
<br><span class=rvts20>1.</span><span class=rvts21>属性，在这里可以分配角色的六维属性点，力量、灵巧、智力、才智、记忆、体质这六种，关于属性、能力和技能详见二楼的技能介绍篇。</span>
<br><span class=rvts20>2.</span><span class=rvts21>能力，分为战斗能力和民事能力，每样能力的加点不仅影响到可习得技能，也会带来一些增益效果。</span>
<br><span class=rvts20>3.</span><span class=rvts21>技能，技能和战斗能力有关，每种战斗能力共有四种初始技能。</span>
<br><span class=rvts20></span>
<br><span class=rvts32>天赋：</span>
<br><img width=1300 height=731 alt="" hspace=1 vspace=1 src="../../assets/pic1/abcamber22.jpg"><span class=rvts20></span>
<br><span class=rvts21>天赋可以带来各种各样的增益效果或一些特殊能力，关于天赋的介绍详见二楼天赋技能篇。</span>
<br><span class=rvts20></span>
<br><span class=rvts32>标签：</span>
<br><img width=1300 height=731 alt="" hspace=1 vspace=1 src="../../assets/pic1/abcamber23.jpg"><span class=rvts20></span>
<br><span class=rvts21>标签可以选择自己的出身</span><span class=rvts20>,</span><span class=rvts21>出身可能会影响到结交伙伴，或者是剧情的发展。</span>
<br><span class=rvts20></span>
<br><span class=rvts32>乐器：</span>
<br><img width=1300 height=731 alt="" hspace=1 vspace=1 src="../../assets/pic1/abcamber24.jpg"><span class=rvts20></span>
<br><span class=rvts21>初始乐器会率先在你的战斗音乐中出现，并高亮你的冒险瞬间。</span></p>
<p><br></p>
<p><br></p>
    </div>
  </div>
</template>

<script>
import BScroll from "better-scroll";
export default {
  data() {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>

<style lang="stylus" type="stylesheet/stylus" scoped>
@import '../../common/stylus/mixin.styl';

.page {
  overflow: hidden;
  height: 100%;
  position: absolute;
  left: 0;
  right: 0;

  .content {
    margin-top: -0.1rem;
    margin-bottom: 0.2rem;
    height: 14rem;
    .rvps2 {
      text-align: justify;
    }
    .rvts17 {
      font-size: 14pt;
      font-family: '宋体';
      font-weight: bold;
      color: #1f497d;
    }

    .rvts20 {
      font-size: 12pt;
      font-family: 'Tahoma', 'Geneva', sans-serif;
    }

    .rvts21 {
      font-size: 12pt;
      font-family: '宋体';
    }
    .rvts31{
      font-size: 14pt;
      font-family: '宋体';
      font-weight: bold;
      color: #c808bf;
    }
    .rvts32{
      font-size: 12pt;
      font-family: '宋体';
      font-weight: bold;
      color: #c808bf;
    }
  }
}
</style>



